@import '../../assets/css/_colors';
@import '../../assets/css/_pixels';
.grabProfileImage {
  padding-top: $small;
  background: url(../../assets/images/profile_bg.png) no-repeat top center;
  background-attachment: fixed;
  background-size: contain;
}
.grabProfileImage img {
  border-radius: 50%;
  max-width: 200px;
  min-width: 80px;

  border: ($small/3) solid $light;
}
